import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import CameraIcon from '@material-ui/icons/PhotoCamera';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import MuiLink from '@material-ui/core/Link';
import ProTip from '~/src/ProTip';
import Link from '~/src/link';
import CircularProgress from '@material-ui/core/CircularProgress';

import Header from '~/src/header';
import Footer from'~/src/footer';
import Head from 'next/head';
import input from '~/src/content';
import superagent from 'superagent';

const ReactMarkdown = require('react-markdown');

const useStyles = makeStyles(theme => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  markdownContainer: {
    backgroundColor: "#ffffff",
    borderRadius: 16,
  }
}));

function MarkdownLoader({url} : {url: string}) {
  // const [loading, setLoading] = useState(false);
  const [text, setText] = React.useState<string>("");
  //https://raw.githubusercontent.com/Tokenyet/MinecraftScepterVersioner/master/content.md

  React.useEffect(() => {
    superagent.get(url).then((response) => {
      setText(response.text);
    });
  });

  return <React.Fragment>
    {
      text == "" ?
      <CircularProgress size={120} />
      :
      <ReactMarkdown source={text} />
    }
  </React.Fragment>
}

export default function Index() {
  const classes = useStyles({});

  return (
    <div style={{ backgroundColor: "#f2f2f2"}}>
      <CssBaseline />
      <Head>
        <link href="/static/github.css" rel="stylesheet" />
      </Head>
      <Header tabIndex={3}/>
      <main>
        <Container maxWidth="lg" className={`markdown-body ${classes.markdownContainer}`} 
          style={{
            marginTop: 24, marginBottom: 24,
            paddingTop: 16, paddingBottom: 16,
            boxShadow: "0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24)",
        }}>
          <MarkdownLoader
            url={"https://raw.githubusercontent.com/Tokenyet/MinecraftScepterVersioner/master/content.md"}
          />
        </Container>
      </main>
      {/* Footer */}
      <Footer style={{ backgroundColor: "#f2f2f2" }} />
      {/* End footer */}
    </div>
  );
}
